.home-buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 5px;
}

.home-buttons > button {
    margin: 5px;
}

.button-2 {
    background-color: #cc4949;
}

.button-3 {
    background-color: #49cc49;
}

.button-4 {
    background-color: #7049cc;
}

.string {
    margin: 0;
    margin-left: 15px;
}

.hash {
    margin: 0;
    margin-left: 15px;
}

.info {
    text-align: justify;
    margin: 10px;
    font-weight: 200;
}